.wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 15px;
    width: 100%;
    height: 100%;
}

.icon {
    animation: show 2s 1;
}

@keyframes show {
    from {
        opacity: 0;
    }
    
    to {
        opacity: 1;
    }
}